
.profile {
  &__info {
    display: flex;
    margin-top: 30px;
    @media (max-width: 767px) {
      flex-direction: column;
      margin-top: 10px;
    }

    p {
      margin: 0;
      display: inline;
    }

    a {
      text-decoration: none;
      color: blue;
      transition: all .3s ease;

      &:hover {
        color: #3761ea;
      }
    }
  }

  &__avatar {
    margin-bottom: 10px;
    max-width: 140px;
    max-height: 140px;
    float: left;
    border-radius: 4px;
    box-shadow: 0 0 4px black;

    @media (max-width: 767px) {
      width: 100%;
      img {
        width: 100%;
      }
    }

    &-wrapper {
      display: flex;
      flex-direction: column;
      margin-right: 10px;
      @media (max-width: 767px) {
        margin-bottom: 25px;
        align-items: center;
        margin-right: 0;
      }
    }
  }

  &__name {
    font-family: 'HistoriaDemo';
    letter-spacing: 3px;
    font-size: 30px;
    line-height: 36px;
    margin-top: -6px;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__status {
    cursor: pointer;
    transition: all .3s ease;

    &:hover {
      color: white;
    }

    &-input {
      border-radius: 4px;
      outline: none;
      padding: 4px 5px;
      border: none;

      &:focus {
        outline: none;
        box-shadow: 0 0 2px black;
      }
    }
  }
}

.contact {
  padding-left: 15px;
  font-style: italic;
}

.checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  > div {
    height: 22px;
  }

  label {
    margin-left: 5px;
    cursor: pointer;
  }

  input {
    width: 15px !important;
    height: 15px !important;
    cursor: pointer;
    margin: 0;
  }
}

.profile__description-form{
  button{
    margin-bottom: 20px;
  }
}